import React, { Component } from "react"

class Index extends Component {

  constructor() {
    super()
    const cumple = new Date("2018-09-15").getTime()
    const now = Date.now()
    const distance = cumple - now
    debugger
    this.state = { cumple, distance }
  }

  componentDidMount() {
    this.timer = setInterval(this.tick, 1000)
  }

  componentWillUnmount() {
    clearInterval(this.timer)
  }
  
  tick = () => {
    const { cumple } = this.state
    const now = Date.now()
    this.setState({ distance: cumple - now })
  }

  render() {
    const { distance } = this.state
    const days = Math.floor(distance / (1000 * 60 * 60 * 24));
    const hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
    const minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
    const seconds = Math.floor((distance % (1000 * 60)) / 1000);
    debugger

    return (
      <div>
        <div className="container">
          <h1 id="head">¡Todavía no!</h1>
          <h2 id="sub">¿Por qué lo abriste?</h2>
          <ul className="countdown">
            <li>
              <span className="days">{days}</span>
            </li>
            <li className="seperator">.</li>
            <li>
              <span className="hours">{hours}</span>
            </li>
            <li className="seperator">:</li>
            <li>
              <span className="minutes">{minutes}</span>
            </li>
            <li className="seperator">:</li>
            <li>
              <span className="seconds">{seconds}</span>
            </li>
          </ul>
        </div>
        <style jsx>{`
        @import url(http://fonts.googleapis.com/css?family=Open+Sans:300,400);
        body {
            background: #363f48;
            color: white;
            font: normal 12px 'Open Sans', sans-serif;
            margin-top: 20px;
        }
        ul.countdown {
            list-style: none;
            margin: 75px 0;
            padding: 0;
            display: block;
            text-align: center;
        }
        ul.countdown li {
            display: inline-block;
        }
        ul.countdown li span {
            font-size: 80px;
            font-weight: 300;
            line-height: 80px;
        }
        ul.countdown li.seperator {
            font-size: 80px;
            line-height: 70px;
            vertical-align: top;
        }
        ul.countdown li p {
            color: #a7abb1;
            font-size: 14px;
        }
        a {
            color: #76949F;
            text-decoration: none;
        }
        a:hover {
            text-decoration: underline;
        }
        .source {
            width: 405px;
            margin: 0 auto;
            background: #4f5861;
            color: #a7abb1;
            font-weight: bold;
            display: block;
            white-space: pre;
            -webkit-border-radius: 3px;
                -moz-border-radius: 3px;
                    border-radius: 3px;
        }
        .btn {
            background: #f56c4c;
            margin: 40px auto;
            padding: 12px;
            display: block;
            width: 100px;
            color: white;
            text-align: center;
            text-transform: uppercase;
            font-weight: bold;
            text-decoration: none;
            -webkit-border-radius: 2px;
                -moz-border-radius: 2px;
                    border-radius: 2px;
        }
        .btn:hover {
            text-decoration: none;
            opacity: .7;
        }
        `}</style>
      </div>
    )
  }
}

export default Index


